section article {
  padding: 10px;
}
.overview {
  display: flex;
  justify-content: space-between;
}
.overview .overview-info {
  flex: 1;
}
.sub-title {
  padding: 5px;
  background-color: #EEE;
}
.work-exp,
.project-exp {
  display: flex;
  justify-content: space-between;
}
.work-exp .work-exp-info,
.project-exp .project-exp-info {
  width: 220px;
  font-size: 18px;
}
.work-exp .work-exp-detail,
.project-exp .project-exp-detail {
  flex: 1;
  /* white-space: pre-line; */
}
.work-exp .work-exp-info .low,
.project-exp .project-exp-info .low {
  font-size: 12px;
  color: #999;
}
.work-exp .work-exp-detail ul {
  list-style: auto;
}
/* .md > ul,
.md > ol,
.md > p {
  margin: 0;
} */
@media only screen and (max-width: 450px) {
  .work-exp,
  .project-exp {
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    border-bottom: 1px solid #eee;
  }
}